<template>
    <div class="row">
      <div id="menu" style="position: fixed; top: 8%; right:16%; z-index: 2">
        <v-menu
          transition="slide-x-transition"
          bottom
          flat
          right
          style="position: absolute; left: 3%"
        >
          <v-btn
            slot="activator"
            dark
            style="background: linear-gradient(to bottom, rgba(23,23,23,0.5) 0%, #a2a2a2 100%);  width: 150px; font-family: 'Roboto', serif; font-size: 14px"
          >
            {{city}}
          </v-btn>
          <v-list>
            <v-list-tile
              v-for="(dc, i) in dcs"
              :key="i"
              v-on:click="proceed(dc[0])"
            >
              <v-list-tile-title>{{ dc[0] }}</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
      </div>
      <div class="col-md-12">
        <div class="card">
          <paper-table :filteredata="filteredata" :filled.sync="filled" :name1="ebash" v-if="mode" style="position: fixed; left: 324px; right: 4%; top: 13.8%; bottom: 10%; overflow-y: auto">
          </paper-table>
          <!--<button v-on:click="go">Click</button>-->
        </div>
      </div>

      <!--<div class="col-md-12">-->
        <!--<div class="card card-plain">-->
          <!--<paper-table type="hover" :title="table1.title" :sub-title="table1.subTitle" :data="table1.data" :columns="table1.columns">-->

          <!--</paper-table>-->
        <!--</div>-->
      <!--</div>-->

    </div>
</template>
<script>
  import PaperTable from '../../UIComponents/PaperTable.vue'
  import VBtn from 'vuetify/es5/components/VBtn'
  import VIcon from 'vuetify/es5/components/VIcon'
  import VMenu from 'vuetify/es5/components/VMenu'
  import { VList, VListTile } from 'vuetify/es5/components/VList'
  import { loadProgressBar } from '../../../../node_modules/axios-progress-bar'
  import '../../../../node_modules/axios-progress-bar/dist/nprogress.css'
  import axios from 'axios'

  export default {
    components: {
      VListTile,
      VMenu,
      VBtn,
      VIcon,
      VList,
      PaperTable
    },
    mounted: function () {
      // this.time()
      // process.env.NODE_TLS_REJECT_UNAUTHORIZED = '0'
      // axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
      //   .then(response => (this.ebash = JSON.stringify(response.data.payload).replace(/"serial":null/gi, '"serial": "none"').replace(/null/gi, '{ "alias": "none", "vendor": "None", "name": "None", "comment": "Free", "_cls": "none" }'))).then(x => (this.ebash = JSON.parse(this.ebash))).then(x => (this.mode = false)).then(x => (this.filteredata = this.ebash)).then(x => (this.mode = true))
      // loadProgressBar()
    },
    methods: {
      go () {
        axios.get('http://10.211.34.8/api/v0/devices')
          .then(response => (this.ebash = JSON.stringify(response.data.payload))).then(x => (this.ebash = JSON.parse(this.ebash))).then(x => (this.mode = false)).then(x => (this.filteredata = this.ebash)).then(x => (this.mode = true))
        loadProgressBar()
      },
      proceed: function (x) {
        var y
        y = x
        x = 'ASH'
        x = y
        // this.ebash = null
        if (x === 'ALL') {
          axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
            .then(response => (this.ebash = JSON.stringify(response.data.payload).replace(/"serial":null/gi, '"serial": "none"').replace(/null/gi, '{ "alias": "none", "vendor": "None", "name": "None", "comment": "Free", "_cls": "none" }'))).then(x => (this.ebash = JSON.parse(this.ebash))).then(x => (this.mode = false)).then(x => (this.filteredata = this.ebash)).then(x => (this.mode = true))
          loadProgressBar()
        } else {
          loadProgressBar()
          axios.get('http://10.211.34.12/api/v0/devices?limit=5000&alias__contains=' + x.toLowerCase())
            .then(response => (this.ebash = JSON.stringify(response.data.payload).replace(/"serial":null/gi, '"serial": "none"').replace(/null/gi, '{ "alias": "none", "name": "None", "vendor": "None", "comment": "Free", "_cls": "none" }'))).then(x => (this.ebash = JSON.parse(this.ebash))).then(x => (this.mode = false)).then(x => (this.filteredata = this.ebash)).then(x => (this.mode = true))
          this.filteredata = this.ebash
        }
        this.filteredata = this.ebash
      }
    },
    data () {
      return {
        ebash: [],
        filteredata: [],
        city: 'Select DC',
        mode: true,
        dcs: [
          ['ALL'],
          ['AMS'],
          ['ASH'],
          ['BER'],
          ['CDG'],
          ['CHI'],
          ['CPH'],
          ['DEN'],
          ['DFW'],
          ['DUB'],
          ['DXB'],
          ['FRA'],
          ['HKG'],
          ['JNB'],
          ['LAX'],
          ['LON'],
          ['MEL'],
          ['MIA'],
          ['MIL'],
          ['MUM'],
          ['NRT'],
          ['NYC'],
          ['OTP'],
          ['PAO'],
          ['PRG'],
          ['SAO'],
          ['SEA'],
          ['SIN'],
          ['SYD'],
          ['YYZ'],
          ['WRW']
        ]
      }
    }
  }

</script>
<style scoped>
  .v-menu > .v-menu__content {
    max-height: 500px;
    overflow-y: auto;
    left: 8px !important;
    top: 40px !important;
  }
  ::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgb(186, 186, 186);
  }
  ::-webkit-scrollbar-thumb {
    background-color: #3c519e;  /*thumb color */
    outline: 2px solid #fffffd;
  }
</style>



