<template>
  <div>
    <apexcharts width="98%" height="98%" type="bar" :options="chartOptions" :series="series"></apexcharts>  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  components: {
    apexcharts: VueApexCharts
  },
  props: {
    computev: {
      type: Number
    },
    computec: {
      type: Number
    },
    sre: {
      type: Number
    },
    suranga: {
      type: Number
    },
    warriors: {
      type: Number
    },
    slpaas: {
      type: Number
    },
    stats: {
      type: Number
    },
    unowned: {
      type: Number
    }
  },
  data: function () {
    return {
      chartOptions: {
        chart: {
          id: 'vuechart-example',
          toolbar: {
            show: false
          }
        },
        xaxis: {
          categories: ['Compute-C', 'Compute-V', 'SRE', 'Stats', 'Streamline PaaS', 'Suranga', 'Warriors-KD', 'Unowned']
        }
      },
      series: [{
        name: 'Devices',
        data: [this.computec, this.computev, this.sre, this.stats, this.slpaas, this.suranga, this.warriors, this.unowned]
      }]
    }
  }
}
</script>

<style scoped>

</style>



