  // import QuadraCard from './QuadraCard'
  // import CiscoCard from './CiscoCard'
  // import SuperMicroCard from './SuperMicroCard'
  import Fill from '../../../charts/Fill'
  import TotalDC from '../../../charts/TotalDC'
  import VProgressLinear from 'vuetify/es5/components/VProgressLinear'
  import VSwitch from 'vuetify/es5/components/VSwitch'
  import { loadProgressBar } from '../../../../node_modules/axios-progress-bar'
  import '../../../../node_modules/axios-progress-bar/dist/nprogress.css'
  import axios from 'axios'
  import PaperNotification from 'src/components/UIComponents/NotificationPlugin/Notification.vue'
  // import { mapGetters } from 'vuex'

  export default {
    name: 'Dash',
    components: {
      VDivider,
      PaperNotification,
      VSwitch,
      DashSMChart,
      VProgressLinear,
      TotalDC,
      Fill,
      // SuperMicroCard,
      // CiscoCard,
      // QuadraCard,
      SUnprov,
      SCompute,
      Sother,
      Quadraup,
      Wantdown,
      Owners,
      Quadra,
      SuperMicro,
      UCS,
      DataTable,
      Total,
      // Countdown,
      Allocated,
      VIcon,
      VBtn,
      Speed,
      DashChart,
      BarDashChart,
      VGrid,
      VDatePicker,
      VFlex,
      VCheckbox,
      VLayout
    },
    props: {
      // dash_val: {
      //   type: Array
      // },
      hwvsall: {
        type: Array
      },
      map: {
        type: Boolean
      },
      nnd_open: {
        type: Boolean
      }
    },
    mounted: function () {
      // while (this.dash_val.length === 0) {
      //   this.charts = false
      //   this.dash_val = this.$store.getters.RESERVE
      //   this.updated = this.$store.getters.UPDATED
      // }
      this.charts = false
      function sleep (ms) {
        return (new Promise(function (resolve, reject) {
          setTimeout(function () { resolve() }, ms)
        }))
      }
      if (this.$store.getters.RESERVE.length === 0) sleep(16000).then(x => (this.dash_val = this.$store.getters.RESERVE)).then(x => (this.updated = this.$store.getters.UPDATED)).then(x => (this.charts = true))
      else if (this.$store.getters.RESERVE.length === 0) sleep(4000).then(x => (this.dash_val = this.$store.getters.RESERVE)).then(x => (this.updated = this.$store.getters.UPDATED)).then(x => (this.charts = true))
      else {
        this.dash_val = this.$store.getters.RESERVE
        this.updated = this.$store.getters.UPDATED
        this.charts = true
      }
      // var start = Date.now()
      // var time = ''
      // axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
      //   .then(response => (this.dash_val = JSON.stringify(response.data.payload).replace(/"_type":null/gi, '"_type": "none"').replace(/null/gi, '{ "alias": "none", "name": "None", "vendor": "None", "comment": "Free", "_cls": "none" }')))
      //   .then(x => (this.dash_val = JSON.parse(this.dash_val)))
      //   .then(x => (this.charts = true))
      //   .then(x => (this.updated = new Date().getUTCMonth() + 1 + ' ' + new Date().getUTCDate() + ' ' + new Date().getUTCFullYear() + ', ' + new Date().getUTCHours() + ':' + new Date().getUTCMinutes()))
      //   .then(function (res) {
      //     var millis = Date.now() - start
      //     res = ('in ' + Math.floor(millis / 1000) + 's')
      //     time = res
      //   })
      //   .then(x => (this.notifyVue('bottom', 'left', 'Connection <b>Established</b>', time)))
      // let z = this.hwvsall
      // this.hwvsall = parseInt(z / this.dash_val.length * 100)
      loadProgressBar()
    },
    methods: {
      notifyVue (verticalAlign, horizontalAlign, message, time) {
        // var color = Math.floor((Math.random() * 4) + 1)
        this.$notifications.notify(
          {
            message: message + '<br>' + time,
            icon: 'ti-plug',
            horizontalAlign: horizontalAlign,
            verticalAlign: verticalAlign,
            type: 'success' // this.type[color]
          })
      },
      go () {
        this.charts = false
        var start = Date.now()
        var time = ''
        axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
          .then(response => (this.dash_val = JSON.stringify(response.data.payload).replace(/"_type":null/gi, '"_type": "none"').replace(/null/gi, '{ "alias": "none", "name": "None", "vendor": "None", "comment": "Free", "_cls": "none" }')))
          .then(x => (this.dash_val = JSON.parse(this.dash_val)))
          .then(x => (this.charts = true))
          .then(x => (this.updated = new Date().getUTCMonth() + 1 + ' ' + new Date().getUTCDate() + ' ' + new Date().getUTCFullYear() + ', ' + new Date().getUTCHours() + ':' + new Date().getUTCMinutes()))
          .then(function (res) {
            var millis = Date.now() - start
            res = ('in ' + Math.floor(millis / 1000) + 's')
            time = res
          })
          .then(x => (this.notifyVue('bottom', 'left', 'Connection <b>Established</b>', time)))
        // let z = this.hwvsall
        // this.hwvsall = parseInt(z / this.dash_val.length * 100)
        loadProgressBar()
      },
      cardinit (x) {
        this.loading = x
        this.card = this.dash_val.filter(f => f._cls.indexOf(x) > -1)
        this.typecard = this.loading
        this.loading = false
        // loadProgressBar()
      },
      timer_init () {
        this.rubikon = this.picker
        this.dp = false
      },
      change () {
        return {
          'backgruond-color': 'white'
        }
      },
      mesg () {
        let x = parseInt(this.hwvsall / this.dash_val.length * 100)
        this.hwvsall = x
        return this.hwvsall
      }
    },
    data () {
      return {
        // bla: [parseInt(this.hwvsall.length / this.dash_val.length * 100)],
        green: 'green',
        rubikon: '12-09-2018',
        card: [],
        target: '',
        updated: '',
        tile: '',
        time: 'khb',
        dash_val: [],
        loading: false,
        vendor: 'all',
        show: false,
        typecard: '',
        quadcard: false,
        ciscocard: false,
        // hwvsall: this.dash_val.filter(f => f.mac_eth0.indexOf() > -1).length / this.dash_val.length * 100,
        picker: null,
        landscape: false,
        reactive: true,
        charts: false,
        // map: false, // Map var
        dwm: false,
        dwm1: false, // Devices with MAC
        type: ['', 'info', 'success', 'warning', 'danger'],
        notifications: {
          topCenter: false
        }
        // sum: BarDashChart.data().sour.toString()
      }
    }
  }
</script>

<style scoped>
  @keyframes progress {
    0% {
      /*noinspection CssInvalidPropertyValue*/
      stroke-dasharray: 0 100;
    }
  }
  ::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgb(186, 186, 186);
  }
  ::-webkit-scrollbar-thumb {
    background-color: #3c59b4;  /*thumb color */
    outline: 2px solid #fffffd;
  }
</style>
