import { Doughnut } from 'vue-chartjs'

export default {
  extends: Doughnut,
  props: {
    ams: {
      type: Number
    },
    ash: {
      type: Number
    },
    ber: {
      type: Number
    },
    chi: {
      type: Number
    },
    dfw: {
      type: Number
    },
    fra: {
      type: Number
    },
    hkg: {
      type: Number
    },
    jnb: {
      type: Number
    },
    lax: {
      type: Number
    },
    lon: {
      type: Number
    },
    mia: {
      type: Number
    },
    nrt: {
      type: Number
    },
    nyc: {
      type: Number
    },
    pao: {
      type: Number
    },
    sao: {
      type: Number
    },
    sea: {
      type: Number
    },
    sin: {
      type: Number
    },
    syd: {
      type: Number
    },
    yyz: {
      type: Number
    }
  },
  data () {
    return {
      datacollection: {
        // Data to be represented on x-axis
        labels: [ 'AMS', 'ASH', 'BER', 'CHI', 'DFW', 'FRA', 'HKG', 'JNB', 'LAX', 'LON', 'MIA', 'NRT', 'NYC', 'PAO', 'SAO', 'SEA', 'SIN', 'SYD', 'YYZ' ],
        datasets: [{
          backgroundColor: ['#d38df8', '#81f882', '#88a5f8', '#6f42c1', 'red', 'yellow', 'grey', 'orange', 'blue', 'brown', 'gold', 'black', '#6610f2', '#565656', '#17a2b8', '#20c997', '#fff', '#e83e8c'],
          pointBackgroundColor: 'white',
          borderWidth: 1,
          pointBorderColor: '#249EBF',
          // Data to be represented on y-axis
          data: [this.ams, this.ash, this.ber, this.chi, this.dfw, this.fra, this.hkg, this.jnb, this.lax, this.lon, this.mia, this.nrt, this.nyc, this.pao, this.sao, this.sea, this.sin, this.syd, this.yyz]
        }
        ]},
      dcam: [1690, 287, 23],
      options: {
        legend: {
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  // Chart.js options that controls the appearance of the chart
  mounted () {
    // renderChart function renders the chart with the datacollection and options object.
    this.renderChart(this.datacollection, this.options)
  }
}



