<template>
  <div id="chart">
    <apexchart type=radialBar :options="chartOptions" :series="series" />
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  components: {
    apexchart: VueApexCharts
  },
  props: {
    owners: {
      type: Array
    }
  },
  methods: {
    count (x) {
      if (this.owners.filter(f => f.owner.alias.indexOf(x) > -1).length * 3 !== 0) {
        this.series += this.owners.filter(f => f.owner.alias.indexOf(x) > -1).length * 3
        this.labels += x
      }
    }
  },
  data: function () {
    return {
      // totaldev: this.owners.length,
      series: [parseInt(this.owners.filter(f => f.owner.alias.indexOf('neteng') > -1).length / this.owners.length * 100),
        parseInt(this.owners.filter(f => f.owner.alias.indexOf('syseng') > -1).length / this.owners.length * 100),
        parseInt(this.owners.filter(f => f.owner.alias.indexOf('sre') > -1).length / this.owners.length * 100),
        parseInt(this.owners.filter(f => f.owner.alias.indexOf('suranga') > -1).length / this.owners.length * 100),
        parseInt(this.owners.filter(f => f.owner.alias.indexOf('warriors') > -1).length / this.owners.length * 100)],
      chartOptions: {
        plotOptions: {
          radialBar: {
            track: {
              background: '#a0a0a0'
            },
            dataLabels: {
              name: {
                fontFamily: 'Consolas',
                fontSize: '18px'
              },
              value: {
                fontFamily: 'Consolas',
                fontSize: '26px'
              },
              total: {
                quan: this.owners.length,
                show: true,
                label: 'Total',
                formatter: function (w) {
                  // By default this function returns the average of all series. The below is just an example to show the use of custom formatter function
                  return this.quan
                }
              }
            }
          }
        },
        labels: ['NetEng', 'SysEng', 'SRE', 'Suranga', 'Warriors-KD']
      }
    }
  }
}
</script>

<style scoped>

</style>



