<template>
  <div>
    <!-- Table Card -->
    <v-card>
      <v-card-title>
        <!-- Search within table -->
        <v-text-field
          v-model="search"
          prepend-icon="search"
          label="Search"
          single-line
          hide-details
          v-on:keyup="mesg"
        ></v-text-field>
        <v-spacer></v-spacer>
        <!-- Dialog warning about export -->
        <v-dialog
          v-model="dialog"
          width="500"
          v-show="filled"
        >
          <v-btn
            slot="activator"
            color="grey lighten-2"
            dark
            small
            icon
          >
            <v-icon small>warning</v-icon>
          </v-btn>

          <v-card>
            <v-card-title
              class="headline grey lighten-2"
              primary-title
            >
              Warning
            </v-card-title>

            <v-card-text>
              Please keep in mind, that export button will not download filtered data, but will export all table instead.<br> Search is visual only for now.
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="primary"
                flat
                v-on:click="dialog = !dialog"
              >
                OK
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- Export XLS area -->
        <v-btn flat round >
          <v-icon disabled
          >file_download</v-icon>
          <json-excel style="position: relative; top: 1%; font-size: 14px"
                      :data   = "filteredata"
                      :name = "'Hydra_export_'+ new Date().getFullYear() + '-' + new Date().getMonth() + '-' + new Date().getDate() + '_' + new Date().getHours() + '-' + new Date().getMinutes()"
                      :fields = "export_fields"
                      v-if="tile !== 'Jacob' && type !== 'Stan'"
          >
            &nbsp; &nbsp;<b>Export XLS</b>
          </json-excel>
          <json-excel style="position: relative; top: 0; font-family: Consolas,serif"
                      :data   = "name1"
                      :fields = "export_fields"
                      :name = "'Hydra_export_'+ new Date().getFullYear() + '-' + new Date().getMonth() + '-' + new Date().getDate() + '_' + new Date().getHours() + '-' + new Date().getMinutes()"
                      v-if="tile === 'Jacob' && type !== 'Stan'">
Export XLS
          </json-excel>
          <json-csv style="position: relative; top: 0; font-family: Consolas,serif;"
                    :data   = "name1"
                    :fields = "csv_export_fields"
                    separator-excel = "true"
                    :name = "'Hydra_export_'+ new Date().getFullYear() + '-' + new Date().getMonth() + '-' + new Date().getDate() + '_' + new Date().getHours() + '-' + new Date().getMinutes() + '.csv'"
                    v-if="type === 'Stan' && tile !== 'Jacob'">
            Export CSV
          </json-csv>
          <json-csv style="position: relative; top: 0; font-family: Consolas,serif"
                    :data   = "name1"
                    separator-excel = "true"
                    :name = "'Hydra_export_'+ new Date().getFullYear() + '-' + new Date().getMonth() + '-' + new Date().getDate() + '_' + new Date().getHours() + '-' + new Date().getMinutes() + '.csv'"
                    v-if="type === 'Stan' && tile === 'Jacob'">
            Export CSV
          </json-csv>
        </v-btn>
        <!--<v-switch-->
          <!--v-model="tile"-->
          <!--color="yellow"-->
          <!--value="Jacob"-->
          <!--persistent-hint-->
          <!--:hint="'Export all'"-->
        <!--&gt;-->
          <!--&lt;!&ndash;<v-switch&ndash;&gt;-->
          <!--&lt;!&ndash;v-model="type"&ndash;&gt;-->
          <!--&lt;!&ndash;color="green"&ndash;&gt;-->
          <!--&lt;!&ndash;value="Stan"&ndash;&gt;-->
          <!--&lt;!&ndash;persistent-hint&ndash;&gt;-->
          <!--&lt;!&ndash;:hint="'Type'"&ndash;&gt;-->
          <!--&lt;!&ndash;&gt;</v-switch>&ndash;&gt;-->
        <!--</v-switch>-->
        <v-spacer></v-spacer>
        <v-spacer></v-spacer>
        <img src="../../assets/load2.gif" style="position: absolute; top: 6%; right: 2%;" v-show="loading"/>
        <v-spacer></v-spacer>
      </v-card-title>
      <!-- Data Table -->
      <v-data-table
        :search="search"
        light
        :rows-per-page-items="[10,25,rpp]"
        :headers="headers"
        :items="desserts"
        :loading="loading"
        class="elevation-1"
      >
        <template slot="items" slot-scope="props">
          <tr v-on:click="meh = !meh, info = props.item, infogen(info)">
            <td>{{ props.item.alias }}</td>
            <td class="text-center">{{ props.item.status.name }}, {{props.item.status.comment}}</td>
            <td class="text-center">{{ props.item.serial }}</td>
            <td class="text-center">{{ props.item.location.name }}</td>
            <!--<td class="text-center">{{ props.item.model.name}}</td>-->
            <td class="text-center" > {{props.item.owner.name}}</td>
          </tr>
        </template>
        <v-alert style="position: absolute; width: 100%; top:54%; left:0; height: 20%;" slot="no-results" :value="true" color="#ffc107" icon="warning">
          Your search for "{{ search }}" found no results.
        </v-alert>
      </v-data-table>
    </v-card>
    <!-- Device Card -->
    <v-card id="devinfo" v-show="meh" style="position: fixed; top: 16%; left: 35%; width: 550px; height: 600px">
      <v-card-title
        class="headline grey lighten-2"
        primary-title
      >
        {{info.hostname}}
      </v-card-title>
      <img v-if="info._cls === 'Device.SuperMicroServer'"
           src="../../assets/supermicro.png" alt=""
           style="position: absolute;
            height: 20%;
            width: 40%;
            left: 30%;
            overflow-y: hidden;
            pointer-events: none;
            border-radius: 2%"/>
      <img v-if="info._cls === 'Device.UCSBlade'"
           src="../../assets/cisco-logo.png" alt=""
           style="position: absolute;
            width: 40%;
            left: 30%;
            overflow-y: hidden;
            pointer-events: none;
            border-radius: 2%"/>
      <v-divider style="position: absolute; top: 12%"></v-divider>
      <!-- Left Column Data -->
      <v-card-text style="position: absolute; top: 25%; left: 3%; font-family: 'Arial Narrow', serif; font-size: 130%">
        <br>
        <br>
        <br>
        <b>Name:</b> {{info.alias}}<br>
        <b>Serial:</b> {{info.serial}}<br>
        <b>Location:</b><br>
        &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; <b>Country:</b> {{country}}<br>
        &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; <b>City:</b> {{city}}<br>
        <b>Vendor:</b> {{vendor}}<br>
        <b>Model:</b> {{model}}<br>
        <div v-if="info._cls === 'Device.SuperMicroServer'">
          <b>IP Address:</b> {{info.ip}}<br>
          <b>IP Address (IPMI):</b> {{ipmi}}<br>
          <b>MAC Address:</b> {{info.macs[0]}}
        </div>
        <div v-if="info._cls === 'Device.UCSBlade'">
          <b>MAC Address:</b> {{info.macs[0]}}<br>
          <b>IP Address (CIMC):</b> {{cimc_hostname}}
        </div><br><br>
      </v-card-text>
      <!-- Right Column Data -->
      <v-card-text style="position: absolute; top: 26%; left: 63%; bottom:5%; font-family: 'Arial Narrow', serif; font-size: 130%">
        <br>
        <br>
        <br>
        <br>
        <b>Status:</b> {{status}}<br>
        <b>Owner:</b> {{owner}}<br>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          style="position: absolute; bottom: 1%; width: 97%"
          color="primary"
          flat
          v-on:click="meh = !meh"
        >
          OK
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
  import {VSpacer} from 'vuetify/es5/components/VGrid'
  import VDataTable from 'vuetify/es5/components/VDataTable/VDataTable'
  import VCard from 'vuetify/es5/components/VCard/VCard'
  import VTextField from 'vuetify/es5/components/VTextField/VTextField'
  import VCardTitle from 'vuetify/es5/components/VCard/VCardTitle'
  import VIcon from 'vuetify/es5/components/VIcon'
  import VBtn from 'vuetify/es5/components/VBtn/VBtn'
  import VAlert from 'vuetify/es5/components/VAlert/VAlert'
  import JsonExcel from 'vue-json-excel'
  import JsonCsv from 'vue-json-csv'
  import VSwitch from 'vuetify/es5/components/VSwitch'
  import VDialog from 'vuetify/es5/components/VDialog/VDialog'
  import VDivider from 'vuetify/es5/components/VDivider'
  import Vue from 'vue'
  import Vuetify from 'vuetify/es5/components/Vuetify'
  // import axios from 'axios'

  Vue.use(Vuetify)
  export default {
    name: 'DT',
    components: {
      VSpacer,
      VDivider,
      VDialog,
      VSwitch,
      JsonCsv,
      JsonExcel,
      VIcon,
      VBtn,
      VAlert,
      VTextField,
      VCard,
      VCardTitle,
      VDataTable
    },
    props: {
      name1: {
        type: Object
      },
      search: {
        type: String
      },
      filled: {
        type: Boolean
      },
      filteredata: {
        type: Array
      }
    },
    data () {
      return {
        totalDesserts: 0,
        desserts: [],
        csdv: 'csv',
        tile: '',
        type: '',
        // [Begin]Device card variables
        info: '',
        meh: false,
        country: '',
        city: '',
        ipmi: '',
        status: '',
        vendor: '',
        model: '',
        owner: '',
        rpp: {'text': '$vuetify.dataIterator.rowsPerPageAll', 'value': -1},
        cimc_hostname: '',
        // [End]Device card variables
        dialog: false,
        loading: true,
        pagination: {},
        export_fields: {
          'Device': 'alias',
          'Status': 'status.name',
          'Serial': 'serial',
          'Location': 'location.name',
          'Vendor': 'model.vendor.name',
          'Model': 'model.name',
          'Owner': 'owner.name'
        },
        csv_export_fields: [
          'alias',
          'payload.status.name',
          'serial',
          'location.name',
          'model.vendor.name',
          'model.name',
          'owner.name'
        ],
        headers: [
          {
            text: 'Devices',
            align: 'left',
            sortable: true, // false,
            value: 'alias'
          },
          { text: 'Status', align: 'center', value: 'status.comment' },
          { text: 'Serial', align: 'center', value: 'serial' },
          { text: 'Location', align: 'center', value: 'location.name' },
          // { text: 'Vendor', align: 'center', value: 'model.name' },
          { text: 'Owner', align: 'center', value: 'owner.name' }
          // { text: 'CIMC', align: 'center', value: 'cimc_hostname' },
          // { text: 'MAC (eth0)', align: 'center', value: 'macs' }
        ]
      }
    },
    watch: {
      pagination: {
        handler () {
          this.getDataFromApi()
            .then(data => {
              this.desserts = data.items
              this.totalDesserts = data.total
            })
        },
        deep: true
      }
    },
    mounted () {
      this.getDataFromApi()
        .then(data => {
          this.desserts = data.items
          this.totalDesserts = data.total
        })
    },
    methods: {
      // exportcsv: function () {
      //   axios.get('http://10.211.34.12/api/v0/devices.csv?limit=5000&alias__contains=' + this.city)
      // },
      infogen (x) {
        if (x.location.country.name === 'None' || !x.location.country) this.country = ''
        else this.country = x.location.country
        if (x.location.name === 'None' || !x.location.name) this.city = ''
        else this.city = x.location.name
        if (x._cls === 'Device.SuperMicro' && x.ipmi_ip.name === 'None') this.ipmi = ''
        else this.ipmi = x.ipmi_ip
        if (x._cls === 'Device.UCSBlade' && x.cimc_ip.name === 'None') this.cimc_hostname = ''
        else this.cimc_hostname = x.cimc_ip
        if (x.model.vendor === 'None' || !x.model.vendor) this.vendor = ''
        else this.vendor = x.model.vendor.name
        if (x.model.name === 'None' || !x.model.name) this.model = ''
        else this.model = x.model.name
        // if (x.status.name === 'None' || !x.status.name) this.status = 'Available'
        // else this.status = x.status.name
        if (x.owner.name === 'None' || !x.owner.name) {
          this.owner = ''
          this.status = 'Available'
        } else {
          this.owner = x.owner.name
          this.status = 'Allocated'
        }
      },
      notnull (x) {
        if (x === '{ "alias": "none", "name": "None", "comment": "Free", "_cls": "none" }') x = 'None'
        return x
      },
      mesg () {
        this.filled = this.search !== ''
      },
      getDataFromApi () {
        this.loading = true
        return new Promise((resolve, reject) => {
          const { sortBy, descending, page, rowsPerPage } = this.pagination

          let items = this.getDesserts()
          const total = 10

          if (this.pagination.sortBy) {
            this.items = items.sort((a, b) => { // todo WTH, sonar is complaining
              const sortA = a[sortBy]
              const sortB = b[sortBy]

              if (descending) {
                if (sortA < sortB) return 1
                if (sortA > sortB) return -1
                return 0
              } else {
                if (sortA < sortB) return -1
                if (sortA > sortB) return 1
                return 0
              }
            })
          }

          if (rowsPerPage > 0) {
            items = items.slice((page - 1) * rowsPerPage, page * rowsPerPage)
          }

          setTimeout(() => {
            this.loading = false
            resolve({
              items,
              total
            })
          }, 1000)
        })
      },
      getDesserts () {
        let wholeArray = []
        wholeArray.push(this.name1)
        console.log(this.name1)
        console.log(wholeArray)
        return this.name1
      }
    }
  }

</script>
<style>

</style>



