      </div>
      <div class="navbar-right-menu">
        <ul class="nav navbar-nav navbar-right">
          <li class="open">
            <!--SEARCH BLOCK-->
            <!--<v-icon style="position: absolute; left: 0.83%; user-select: none" dark>search</v-icon>-->
            <div id="chips" style="position: absolute; bottom: 23px; right: 343px; font-size: 14px; height: 10px; user-select: none; width: 400px">
              <v-btn icon small v-if="chip" v-on:click="chip = false, mode = '', cererus = ''"><v-icon>close</v-icon></v-btn>
            <v-chip
