                </v-card-text>
                <v-text-field
                  v-model="report"
                  label="Type here"
                  style="position: absolute; width: 80%; left: 10%; top: 55%"
                  v-on:keyup="state = true"
                ></v-text-field>
                <div id="transaction-cover" style="position: absolute; top: 71px; width: 300px; height: 229px; border-radius: 4%; background-color: rgba(255,255,255,0.98)" v-if="loading"></div>
                <img src="../../../assets/success.gif" style="position: absolute; top: 110px; left: 90px" v-show="loading"/>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <!--<v-icon v-show="seen==='Table'">tab</v-icon>-->
                  <v-btn
                    color="light"
                    large
                    flat
                    v-on:click="feedback()"
                    style="position: absolute; bottom: 5%; width: 90%; left: 2%;"
                    v-bind:style="[state ? {'background-color': 'transparent'} : {'background-color': '#ff161694'}]"
                  >
                    Submit
                  </v-btn>
                  <v-btn
                    color="dark"
                    small
                    icon
                    v-on:click="jack = false; state = true; report = ''"
                    style="position: absolute; top: 2%;right: 2%"
                  ><v-icon small color="#424242">close</v-icon>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <!-- About -->
            <v-dialog
              v-model="dialog"
              width="600"

            >
              <v-btn
                style="position: relative; top: 12px"
                slot="activator"
                icon
                v-on:click="cererus = ''; mode=''"
              >
                <v-icon color="#9a9ea8">help</v-icon>
              </v-btn>
              <v-card style="position: fixed; top: 30%; left: 30%; width: 800px">
                <v-card-title
                  class="headline grey lighten-2"
                  primary-title
                  style="color: #424242"
                >
                  About
                </v-card-title>

                <v-card-text style="text-align: center; margin-right: 2px; margin-left: 2px">
                  &nbsp;Project Hydra:Synergy is developed for inventory purposes, and has an API usage as well as GUI.<br>
                  &nbsp;Graphical interface dedicated to represtent data in table or visual view<br>
                  &nbsp;To get a bit more acquainted, you can try <br>
                  <v-btn v-on:click="overview" flat><b><u>Quick GUI Overview</u></b></v-btn><br><br><br>
                  &nbsp;<i>&copy; Project:Hydra_Synergy, inventory tool, device66, 2018-2019</i>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <!--<v-icon v-show="seen==='Table'">tab</v-icon>-->
