                    color="light"
                    large
                    v-on:click="dialog = false;"
                  >
                    OK
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </li>
          <li>
            <!--LOGOUT BUTTON-->
            <v-btn icon v-on:click="logout" style="position: relative; top: 12px; margin-right: 10px">
              <v-icon color="#9a9ea8">logout</v-icon>
            </v-btn>
          </li>
        </ul>
      </div>
    </div>
    <div>
      <DataTable :name1="devices" v-if="mode === 'table' && chip" style="position: fixed; top:11%; bottom: 10%; left: 30%; right: 30%">
      </DataTable>
    </div>
    <v-dialog
      v-model="no_results"
      width="500"
    >
      <v-card style="position: absolute; top: 20%; left:25%; right: 25%;">
        <v-card-title
          class="headline grey lighten-2"
          primary-title
          style="color: #424242"
        >
          Search rules
        </v-card-title>

        <v-card-text style="text-align: left; position: relative; left: 20px">
          <b>No results found</b><br>
          Maybe you've just mistyped. Here are rules of search engine currently running:<br>
          - If you want to find a <b>MAC</b> program will understand you by ':' symbol in your request<br>
          - If you want to find an <b>IP address</b>, you should specify it like x.x.x.x (One or two octets instead of four won't procceed).<br> Please note that IPMI addresses of SuperMicro devices type is not searchable yet<br>
          - <b>Location</b> can be found by specifying airport code of desired one<br>
          - If you want to find a <b>team</b> try to avoid spaces in specifying. (e.g. 'sre', 'computecore', 'warriors-kd' etc.)<br>
          If nothing of above didn't bring any results, system will check name (alias), serial and model fields.<br> After that search will have no results
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <!--<v-icon v-show="seen==='Table'">tab</v-icon>-->
          <v-btn
            color="light"
            large
            flat
            v-on:click="no_results = false"
            style="width: 98%;"
          >
            OK
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <div id="loadscr" v-if="$store.getters.RESERVE.length === 0">
    <div id="blur" style="position: fixed; top:0; left:0; width:100%; height: 100%; background-color: rgba(208,208,208,0.97); filter: blur(50px); color: black">
    </div>
      <span class="dot" style="position: fixed; top: 17px; left: 19px;"><img src="../../../assets/load7.gif" style="position: relative; left: 7px; top: 7px; height: 25px" /></span>
      <!--<div class="lds-ellipsis" style="position: relative; top: 27px"><div></div><div></div><div></div><div></div></div>-->
      <div id="text" style="position: fixed; top: 20%; font-size: 72px">Compute Services Team Inventory System is preparing<br>Please wait<div class="lds-ellipsis" style="position: relative; top: 27px"><div></div><div></div><div></div><div></div></div></div>
      <div id="quote" style="position: fixed; top: 60%; right: 10%; font-size: 24px"><i>{{quote}}</i></div>
    </div>
  </nav>
</template>
<script>
  import VTextField from 'vuetify/es5/components/VTextField/VTextField'
  import VBtn from 'vuetify/es5/components/VBtn/VBtn'
  import VIcon from 'vuetify/es5/components/VIcon/VIcon'
  import VCard from 'vuetify/es5/components/VCard/VCard'
  import VToolbar from 'vuetify/es5/components/VToolbar/VToolbar'
  import VInput from 'vuetify/es5/components/VInput/VInput'
  import VContainer from 'vuetify/es5/components/VGrid/VContainer'
  import VLayout from 'vuetify/es5/components/VGrid/VLayout'
  import VFlex from 'vuetify/es5/components/VGrid/VFlex'
  import VForm from 'vuetify/es5/components/VForm/VForm'
  import VDialog from 'vuetify/es5/components/VDialog/VDialog'
  import VCardTitle from 'vuetify/es5/components/VCard/VCardTitle'
  import VDivider from 'vuetify/es5/components/VDivider/VDivider'
  import VBadge from 'vuetify/es5/components/VBadge/VBadge'
  import 'axios-progress-bar/dist/nprogress.css'
  import axios from 'axios'
  import * as emailjs from 'emailjs-com'
  import VTooltip from 'vuetify/es5/components/VTooltip'
  import VSnackbar from 'vuetify/es5/components/VSnackbar/VSnackbar'
  import Vue from 'vue'
  import DataTable from '../../UIComponents/PaperTable'
  import Vuetify from 'vuetify/es5/components/Vuetify'
  import VChip from 'vuetify/es5/components/VChip'

  Vue.use(Vuetify)

  export default {
    components: {
      VChip,
      DataTable,
      VSnackbar,
      VTooltip,
      VBadge,
      VDivider,
      VCardTitle,
      VDialog,
      VBtn,
      VForm,
      VContainer,
      VLayout,
      VFlex,
      VInput,
      VTextField,
      VToolbar,
      VCard,
      VIcon
    },
    mounted: function () {
      var variant = Math.floor(Math.random() * 6)
      if (variant === 0) this.quote = 'The Internet is becoming the town square for the global village of tomorrow. \r~ Bill Gates'
      if (variant === 1) this.quote = 'It has become appallingly obvious that our technology has exceeded our humanity. \n Albert Einstein'
      if (variant === 2) this.quote = 'Never trust a computer you cant throw out a window. \n Steve Wozniak'
      if (variant === 3) this.quote = 'Everything is designed. Few things are designed well. \n Brian Reed'
      if (variant === 4) this.quote = 'You don\'t start communities. Communities already exist. The question to ask is how you can help them do that better.\n  Mark Zuckerberg'
      if (variant === 5) this.quote = 'Intelligence is the ability to avoid doing work, yet getting the work done.  Linus Torvalds'
      var start = Date.now()
      var time = ''
      this.$store.dispatch('GET_RESERVE')
        .then(x => (this.reserve = this.$store.getters.RESERVE))
        .then(function (res) {
          var millis = Date.now() - start
          res = ('in ' + Math.floor(millis / 1000) + 's')
          time = res
        })
        .then(x => (this.notifyVue('bottom', 'left', 'Connection <b>Established</b>', time)))
        .then(x => (this.updated = new Date().getUTCMonth() + 1 + ' ' + new Date().getUTCDate() + ' ' + new Date().getUTCFullYear() + ', ' + new Date().getUTCHours() + ':' + new Date().getUTCMinutes()))
        .then(x => (this.$store.commit('SET_UPDATED', this.updated)))
      // axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
      //   .then(response => (this.reserve = JSON.stringify(response.data.payload).replace(/"macs":\[\]/gi, '"macs": ["none"]').replace(/"cimc_ip":null/gi, '"cimc_ip": "none"').replace(/"ip":null/gi, '"ip": "none"').replace(/"ipmi_ip":null/gi, '"ipmi_ip": "none"').replace(/"serial":null/gi, '"serial": "none"').replace(/"_type":null/gi, '"_type": "none"').replace(/null/gi, '{ "alias": "none", "name": "None", "vendor": "None", "comment": "Free", "_cls": "none" }')))
      //   .then(x => (this.reserve = JSON.parse(this.reserve)))
    },
    computed: {
      routeName () {
        const {name} = this.$route
        return this.capitalizeFirstLetter(name)
      }
    },
    data () {
      return {
        cererus: '',
        updated: '',
        jack: false,
        alerts: ['{ "name": "No", "overall_state": "Data"}'],
        host: '',
        mode: '',
        devices: [],
        reserve: [],
        locations: [
          'AMS',
          'ASH',
          'BER',
          'CDG',
          'CHI',
          'CPH',
          'DEN',
          'DFW',
          'DUB',
          'DXB',
          'FRA',
          'HKG',
          'JNB',
          'LAX',
          'LON',
          'MEL',
          'MIA',
          'MIL',
          'MUM',
          'NRT',
          'NYC',
          'OTP',
          'PAO',
          'PRG',
          'SAO',
          'SEA',
          'SIN',
          'SYD',
          'YYZ',
          'WRW'
        ],
        teams: ['compute-c', 'compute-v', 'stats', 'streamline-paas', 'syseng', 'site-reliability-engineering', 'suranga', 'warriors', 'unowned'],
        no_results_icon: false,
        no_results: false,
        chip: false,
        match: 0,
        fields: ['lol'],
        buff: [],
        quote: '',
        search_field: '',
        loading: false,
        loading_srch: false,
        report: '',
        dialog: false,
        state: true,
        activeNotifications: false

      }
    },
    methods: {
      notifyVue (verticalAlign, horizontalAlign, message, time) {
        // var color = Math.floor((Math.random() * 4) + 1)
        this.$notifications.notify(
          {
            message: message + '<br>' + time,
            icon: 'ti-plug',
            horizontalAlign: horizontalAlign,
            verticalAlign: verticalAlign,
            type: 'success' // this.type[color]
          })
      },
      swap (x) {
        // eslint-disable-next-line
        var blank
        // this.mode = ''
        if (x === 'UCS MAC') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f._cls.indexOf('UCS') > -1).filter(f => f.macs[0].indexOf(this.cererus) > -1)))
            .then(y => (this.search_field = 'UCS MAC'))
            .then(y => (this.mode = 'table'))
        }
        if (x === 'SM MAC') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f._cls.indexOf('Micro') > -1).filter(f => f.macs[0].indexOf(this.cererus) > -1)))
            .then(y => (this.search_field = 'SM MAC'))
            .then(y => (this.mode = 'table'))
        }
        if (x === 'SM IP Addr') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
              .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f._cls.indexOf('Micro') > -1).filter(f => f.ipmi_ip.indexOf(this.cererus) > -1)))
            .then(y => (this.search_field = 'SM IP Addr'))
            .then(y => (this.mode = 'table'))
        }
        if (x === 'UCS IP Addr') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f._cls.indexOf('UCS') > -1).filter(f => f.cimc_ip.indexOf(this.cererus.toString()) > -1)))
            .then(y => (this.search_field = 'UCS IP Addr'))
            .then(y => (this.mode = 'table'))
        }
        if (x === 'Location') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f.location.alias.indexOf(this.cererus.toLowerCase()) > -1)))
            .then(y => (this.search_field = 'Location'))
            .then(y => (this.mode = 'table'))
        }
        var temp = this.cererus
        if (this.cererus === 'unowned') temp = 'none'
        if (x === 'Team') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f.owner.alias.indexOf(temp.toLowerCase()) > -1)))
            .then(y => (this.search_field = 'Team'))
            .then(y => (this.mode = 'table'))
        }
        if (x === 'Model') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f.model.alias.indexOf(this.cererus.toLowerCase()) > -1)))
            .then(y => (this.search_field = 'Model'))
            .then(y => (this.mode = 'table'))
        }
        if (x === 'Name') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f.alias.indexOf(this.cererus.toLowerCase()) > -1)))
            .then(y => (this.search_field = 'Name'))
            .then(y => (this.mode = 'table'))
        }
        if (x === 'Serial') {
          axios.get('http://10.211.34.12/api/v0/devices')
            .then(response => (blank = JSON.stringify(response.data.count)))
            .then(y => (this.mode = ''))
            .then(y => (this.devices = this.reserve.filter(f => f.serial.indexOf(this.cererus) > -1)))
            .then(y => (this.search_field = 'Serial'))
            .then(y => (this.mode = 'table'))
        }
        blank += 1
      },
      foundin () {
        var arr = []
        if (this.reserve.filter(f => f._cls.indexOf('UCS') > -1).filter(f => f.macs[0].indexOf(this.cererus) > -1).length !== 0) arr.push('UCS MAC')
        if (this.reserve.filter(f => f._cls.indexOf('Micro') > -1).filter(f => f.macs[0].indexOf(this.cererus) > -1).length !== 0) arr.push('SM MAC')
        if (this.reserve.filter(f => f._cls.indexOf('Micro') > -1).filter(f => f.ipmi_ip.indexOf(this.cererus) > -1).length !== 0) arr.push('SM IP Addr')
        if (this.reserve.filter(f => f._cls.indexOf('UCS') > -1).filter(f => f.cimc_ip.indexOf(this.cererus.toString()) > -1).length !== 0) arr.push('UCS IP Addr')
        if (this.reserve.filter(f => f.location.alias.indexOf(this.cererus.toLowerCase()) > -1).length !== 0) arr.push('Location')
        var temp = this.cererus
        if (this.cererus === 'unowned') temp = 'none'
        if (this.reserve.filter(f => f.owner.alias.indexOf(temp.toLowerCase()) > -1).length !== 0) arr.push('Team')
        if (this.reserve.filter(f => f.model.alias.indexOf(this.cererus.toLowerCase()) > -1).length !== 0) arr.push('Model')
        if (this.reserve.filter(f => f.alias.indexOf(this.cererus.toLowerCase()) > -1).length !== 0) arr.push('Name')
        if (this.reserve.filter(f => f.serial.indexOf(this.cererus) > -1).length !== 0) arr.push('Serial')
        this.fields = arr
        console.log(arr)
      },
      find2 () {
        this.foundin()
        // this.$router.replace(this.$route.query.redirect || '/admin/table-list')
        var re = new RegExp('^(?:[0-9]{1,3}.){3}[0-9]{1,3}$') // Regular expression of IP pattern
        if (this.cererus.includes(':')) { // Recognition if entered value is MAC address
          this.devices = this.reserve.filter(f => f._cls.indexOf('UCS') > -1).filter(f => f.macs[0].indexOf(this.cererus) > -1)
          if (this.devices.length === 0) this.devices = this.reserve.filter(f => f._cls.indexOf('Micro') > -1).filter(f => f.macs[0].indexOf(this.cererus) > -1)
          this.search_field = 'MAC'
          this.chip = true
          this.mode = 'table'
        } else if (re.test(this.cererus)) { // Maybe it's an IP address?
          // this.devices = this.reserve.filter(f => f._cls.indexOf('Micro') > -1).filter(f => f.ip.indexOf(this.cererus) > -1)
          if (this.devices.length === 0) this.devices = this.reserve.filter(f => f._cls.indexOf('Micro') > -1).filter(f => f.ipmi_ip.indexOf(this.cererus) > -1)
          if (this.devices.length === 0) this.devices = this.reserve.filter(f => f._cls.indexOf('UCS') > -1).filter(f => f.cimc_ip.indexOf(this.cererus.toString()) > -1)
          this.search_field = 'IP Address'
          this.chip = true
          this.mode = 'table'
          console.log('IP search')
        } else if (this.locations.includes(this.cererus.toUpperCase())) { // Recognition if value is DC
          this.devices = this.reserve.filter(f => f.location.alias.indexOf(this.cererus.toLowerCase()) > -1)
          this.search_field = 'Location'
          this.chip = true
          this.mode = 'table'
        } else if (this.teams.includes(this.cererus.toLowerCase())) { // All right, then probably it's a team
          var temp = this.cererus
          if (this.cererus === 'unowned') temp = 'none'
          this.devices = this.reserve.filter(f => f.owner.alias.indexOf(temp.toLowerCase()) > -1)
          this.search_field = 'Team'
          this.chip = true
          this.mode = 'table'
        } else {
          this.devices = this.reserve.filter(f => f.model.alias.indexOf(this.cererus.toLowerCase()) > -1)
          this.search_field = 'Model'// Maybe model?
          this.chip = true
          if (this.devices.length === 0) {
            this.devices = this.reserve.filter(f => f.alias.indexOf(this.cererus.toLowerCase()) > -1) // look for it in alias
            this.search_field = 'Name'
            this.chip = true
          }
          if (this.devices.length === 0) {
            this.devices = this.reserve.filter(f => f.serial.indexOf(this.cererus) > -1)
            this.search_field = 'Serial'
            this.chip = true
            console.log('serial')
          } // Didn't find it in alias? Well then it's a serial for sure
          this.mode = 'table'
        }
        if (this.devices.length === 0) {
          this.no_results_icon = true
          this.search_field = 'No results found'
          this.chip = false
          this.mode = ''
        } // I give up on finding that
      },
      feedback () {
        var templateParams = {
          to_name: 'dude',
          message_html: this.report,
          from_name: 'hydra project'
        }
        if (this.report !== '') {
          this.loading = true
          emailjs.send('gmail', 'template_eWrRz0wR', templateParams, 'user_Ag3EpPO7p8vp0TQV4Jj7H')
            .then(function (response) {
              console.log('SUCCESS!', response.status, response.text)
            }, function (error) {
              console.log('FAILED...', error)
            }).then(x => (this.loading = false)).then(x => (this.jack = false))
        } else this.state = false
      },
      capitalizeFirstLetter (string) {
        return string.charAt(0).toUpperCase() + string.slice(1)
      },
      toggleNotificationDropDown () {
        this.activeNotifications = !this.activeNotifications
      },
      closeDropDown () {
        this.activeNotifications = false
      },
      toggleSidebar () {
        this.$sidebar.displaySidebar(!this.$sidebar.showSidebar)
      },
      hideSidebar () {
        this.$sidebar.displaySidebar(false)
      }
    }
  }

</script>
<style>
  .lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
  }
  .lds-ellipsis div {
    position: absolute;
    top: 27px;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background: #66615b;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  .lds-ellipsis div:nth-child(1) {
    left: 6px;
    animation: lds-ellipsis1 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(2) {
    left: 6px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(3) {
    left: 26px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  .lds-ellipsis div:nth-child(4) {
    left: 45px;
    animation: lds-ellipsis3 0.6s infinite;
  }
  @keyframes lds-ellipsis1 {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
  }
  @keyframes lds-ellipsis3 {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0);
    }
  }
  @keyframes lds-ellipsis2 {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(19px, 0);
    }
  }
  .dot {
    height: 40px;
    width: 40px;
    background-color: #ffffff;
    border-radius: 50%;
    display: inline-block;
  }

</style>



