<template>
  <div :class="sidebarClasses" :data-background-color="backgroundColor" :data-active-color="activeColor">
    <!--
            Tip 1: you can change the color of the sidebar's background using: data-background-color="white | black | darkblue"
            Tip 2: you can change the color of the active button using the data-active-color="primary | info | success | warning | danger"
        -->
    <!-- -->
    <div class="sidebar-wrapper" id="style-3">
      <div class="logo">
        <a href="#" class="simple-text" style="font-size: 10.5px">
            <div class="logo-img">
                <img src="static/img/tb.png" style="position: relative; top: -1px; left: 1px" alt="">
            </div>
          Compute Team Inventory System
        </a>
      </div>
      <slot>

      </slot>
      <ul :class="navClasses">
        <!--<v-btn flat dark v-on:click="gofk" style="width: 80%; text-align: left"><span class="ti-dashboard"></span> &nbsp; &nbsp;Dashboard</v-btn>-->
        <!--<v-btn flat dark v-on:click="gofk" style="width: 80%; text-align: left"><span class="ti-view-list-alt"></span> &nbsp; &nbsp;Table&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;</v-btn>-->
        <!--<v-btn flat dark v-on:click="gofk" style="width: 80%"><span class="ti-map"></span> &nbsp; &nbsp; Map &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;</v-btn>-->
        <!--<v-btn flat dark v-on:click="gofk" style="width: 80%"><span class="ti-user"></span> User Profile</v-btn>-->
        <!--By default vue-router adds an active class to each route link. This way the links are colored when clicked-->
        <router-link v-for="(link,index) in sidebarLinks" :to="link.path" tag="li" :ref="link.name" v-on:click="gofk">
          <a>
            <i :class="link.icon"></i>

            <p>{{link.name}}
            </p>
          </a>
        </router-link>
      </ul>
      <moving-arrow :move-y="arrowMovePx">

      </moving-arrow>
    </div>
  </div>
</template>
<script>
  import VTextField from 'vuetify/es5/components/VTextField/VTextField'
  import VBtn from 'vuetify/es5/components/VBtn/VBtn'
  import VIcon from 'vuetify/es5/components/VIcon/VIcon'
  import VFlex from 'vuetify/es5/components/VGrid/VFlex'
  import MovingArrow from './MovingArrow.vue'
  export default {
    props: {
      type: {
        type: String,
        default: 'sidebar',
        validator: (value) => {
          let acceptedValues = ['sidebar', 'navbar']
          return acceptedValues.indexOf(value) !== -1
        }
      },
      backgroundColor: {
        type: String,
        default: 'black',
        validator: (value) => {
          let acceptedValues = ['white', 'black', 'darkblue']
          return acceptedValues.indexOf(value) !== -1
        }
      },
      activeColor: {
        type: String,
        default: 'info',
        validator: (value) => {
          let acceptedValues = ['primary', 'info', 'success', 'warning', 'danger']
          return acceptedValues.indexOf(value) !== -1
        }
      },
      sidebarLinks: {
        type: Array,
        default: () => []
      }
    },
    components: {
      MovingArrow,
      VTextField,
      VBtn,
      VFlex,
      VIcon
    },
    computed: {
      sidebarClasses () {
        if (this.type === 'sidebar') {
          return 'sidebar'
        } else {
          return 'collapse navbar-collapse off-canvas-sidebar'
        }
      },
      navClasses () {
        if (this.type === 'sidebar') {
          return 'nav'
        } else {
          return 'nav navbar-nav'
        }
      },
      /**
       * Styles to animate the arrow near the current active sidebar link
       * @returns {{transform: string}}
       */
      arrowMovePx () {
        return this.linkHeight * this.activeLinkIndex
      }
    },
    data () {
      return {
        linkHeight: 60,
        activeLinkIndex: 0,
        reserve: [],

        windowWidth: 0,
        isWindows: false,
        hasAutoHeight: false
      }
    },
    methods: {
      gofk () {
        // eslint-disable-next-line
        this.$router.push({ name: 'maps', params: { map_val: this.reserve }})
      },
      findActiveLink () {
        this.sidebarLinks.find((element, index) => {
          let found = element.path === this.$route.path
          if (found) {
            this.activeLinkIndex = index
          }
          return found
        })
      }
    },
    mounted () {
      this.findActiveLink()
      // axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
      //   .then(response => (this.reserve = JSON.stringify(response.data.payload).replace(/"macs":\[\]/gi, '"macs": ["none"]').replace(/"cimc_ip":null/gi, '"cimc_ip": "none"').replace(/"ip":null/gi, '"ip": "none"').replace(/"ipmi_ip":null/gi, '"ipmi_ip": "none"').replace(/"serial":null/gi, '"serial": "none"').replace(/"_type":null/gi, '"_type": "none"').replace(/null/gi, '{ "alias": "none", "name": "None", "vendor": "None", "comment": "Free", "_cls": "none" }')))
      //   .then(x => (this.reserve = JSON.parse(this.reserve)))
    },
    watch: {
      $route: function (newRoute, oldRoute) {
        this.findActiveLink()
      }
    }
  }

</script>
<style>

</style>



