import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { loadProgressBar } from 'axios-progress-bar'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    reserve: [],
    updated: null
  },
  getters: {
    RESERVE: state => {
      return state.reserve
    },
    UPDATED: state => {
      return state.updated
    }
  },
  mutations: {
    SET_UPDATED: (state, payload) => {
      state.updated = payload
    },
    SET_RESERVE: (state, payload) => {
      state.reserve = payload
    },
    ADD_RESERVE: (state, payload) => {
      state.reserve.push(payload)
    }
  },
  actions: {
    GET_RESERVE: async (context, payload) => {
      var data
      await axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
        .then(response => (data = JSON.stringify(response.data.payload).replace(/"macs":\[\]/gi, '"macs": ["none"]').replace(/"cimc_ip":null/gi, '"cimc_ip": "none"').replace(/"ip":null/gi, '"ip": "none"').replace(/"ipmi_ip":null/gi, '"ipmi_ip": "none"').replace(/"serial":null/gi, '"serial": "none"').replace(/"_type":null/gi, '"_type": "none"').replace(/null/gi, '{ "alias": "none", "name": "None", "vendor": "None", "comment": "Free", "_cls": "none" }')))
        .then(x => (data = JSON.parse(data)))
      loadProgressBar()
      context.commit('SET_RESERVE', data)
    }
  }
})



