import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  props: {
    otp: {
      type: Number
    },
    cdg: {
      type: Number
    },
    cph: {
      type: Number
    },
    den: {
      type: Number
    },
    dub: {
      type: Number
    },
    dxb: {
      type: Number
    },
    mel: {
      type: Number
    },
    mil: {
      type: Number
    },
    mum: {
      type: Number
    },
    prg: {
      type: Number
    },
    wrw: {
      type: Number
    },
    syd: {
      type: Number
    },
    ams: {
      type: Number
    },
    ash: {
      type: Number
    },
    ber: {
      type: Number
    },
    chi: {
      type: Number
    },
    dfw: {
      type: Number
    },
    fra: {
      type: Number
    },
    jnb: {
      type: Number
    },
    yvr: {
      type: Number
    },
    lax: {
      type: Number
    },
    mia: {
      type: Number
    },
    nyc: {
      type: Number
    },
    pao: {
      type: Number
    },
    sea: {
      type: Number
    },
    sin4: {
      type: Number
    },
    nrt: {
      type: Number
    },
    yyz: {
      type: Number
    },
    lon: {
      type: Number
    },
    hkg: {
      type: Number
    },
    sao: {
      type: Number
    }
  },
  data () {
    return {
      datacollection: {
        // Data to be represented on x-axis
        labels: [ 'AMS', 'ASH', 'BER', 'CDG', 'CHI', 'CPH', 'DEN', 'DFW', 'DUB', 'DXB', 'FRA', 'HKG', 'JNB', 'LAX', 'LON', 'MEL', 'MIA', 'MIL', 'MUM', 'NRT', 'NYC', 'OTP', 'PAO', 'PRG', 'SAO', 'SEA', 'SIN', 'SYD', 'YYZ', 'WRW' ],
        datasets: [{
          label: 'Cisco',
          backgroundColor: '#88A5F8',
          pointBackgroundColor: 'white',
          borderWidth: 1,
          pointBorderColor: '#249EBF',
          // Data to be represented on y-axis
          data: ['', '', '', this.cdg, '', this.cph, this.den, '', this.dub, this.dxb, '', '', '', '', '', this.mel, '', this.mil, this.mum, '', '', this.otp, '', this.prg, '', '', '', '', '', this.wrw]
        },
        {
          label: 'SuperMicro',
          backgroundColor: '#ff6f77',
          pointBackgroundColor: 'white',
          borderWidth: 1,
          pointBorderColor: '#249EBF',
          // Data to be represented on y-axis
          data: [ this.ams, this.ash, this.ber, '', this.chi, '', '', this.dfw, '', '', this.fra, this.hkg, this.jnb, this.lax, this.lon, '', this.mia, '', '', this.nrt, this.nyc, '', this.pao, '', this.sao, this.sea, this.sin4, this.syd, this.yyz, '' ]
        }
        ]
      },
      // Chart.js options that controls the appearance of the chart
      sour: 2013,
      options: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true,
              fontColor: 'black'
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            ticks: {
              fontColor: 'black'
            },
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          labels: {
            fontColor: 'black'
          },
          display: true
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted () {
    // renderChart function renders the chart with the datacollection and options object.
    this.renderChart(this.datacollection, this.options)
  }
}



