import { Doughnut } from 'vue-chartjs'

export default {
  extends: Doughnut,
  props: {
    otp: {
      type: Number
    },
    cdg: {
      type: Number
    },
    cph: {
      type: Number
    },
    den: {
      type: Number
    },
    dub: {
      type: Number
    },
    dxb: {
      type: Number
    },
    lon: {
      type: Number
    },
    mel: {
      type: Number
    },
    mil: {
      type: Number
    },
    mum: {
      type: Number
    },
    prg: {
      type: Number
    },
    wrw: {
      type: Number
    }
  },
  data () {
    return {
      datacollection: {
        // Data to be represented on x-axis
        labels: [ 'CDG', 'CPH', 'DEN', 'DUB', 'DXB', 'LON', 'MEL', 'MIL', 'MUM', 'OTP', 'PRG', 'WRW' ],
        datasets: [{
          backgroundColor: ['#d38df8', '#81f882', '#88a5f8', 'red', 'yellow', 'grey', 'orange', 'blue', 'brown', 'gold', 'black'],
          pointBackgroundColor: 'white',
          borderWidth: 1,
          pointBorderColor: '#249EBF',
          // Data to be represented on y-axis
          data: [this.cdg, this.cph, this.den, this.dub, this.dxb, this.lon, this.mel, this.mil, this.mum, this.otp, this.prg, this.wrw]
        }
        ]},
      dcam: [1690, 287, 23],
      options: {
        legend: {
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  // Chart.js options that controls the appearance of the chart
  mounted () {
    // renderChart function renders the chart with the datacollection and options object.
    this.renderChart(this.datacollection, this.options)
  }
}



