<template>
  <div style="font-size: 33px; position: relative; bottom: 1%;text-shadow: 2px 2px #4664a6;">
    <br>
   {{sum}}
  </div>
</template>

<script>
export default {
  props: {
    sum: {
      type: Number
    }
  },
  data: function () {
    return {
      labels: ['Average Results']
    }
  }
}
</script>

<style scoped>
  .circular-chart {
    display: block;
    margin: 10px auto;
    max-width: 80%;
    max-height: 250px;
  }

  .circle {
    fill: none;
    stroke-linecap: round;
    animation: progress  2s ease-out forwards;
  }

  .circle-bg {
    fill: none;
    stroke-linecap: round;
    animation: 1s ease-out forwards;
  }

  @keyframes progress {
    0% {
      /*noinspection CssInvalidPropertyValue*/
      stroke-dasharray: 0 100;
    }
  }
</style>



