<template>
  <div name="locationcard">
    <!-- Free chart -->
    <v-btn depressed style="position: absolute; bottom:65%; left: 1%; height: 30%; min-height:80px; width: 10%;background-color: rgba(245,245,245,0.41)"
           v-bind:style="[csrch !== 'Free' ? {'background-color': 'rgba(245,245,245,0.41)'} : {'background-color': 'white'}]"
           v-on:click="filtered('Free')">
      <SCompute :series="[parseInt(hwvsall.filter(f => f.owner.name.indexOf('None') > -1).length/hwvsall.length*100)]"></SCompute>
      <v-text style="position: absolute; bottom: 25%; font-size: 12px">Status: <br><b>Available</b></v-text>
    </v-btn>
    <!-- Unavailable chart -->
    <v-btn depressed style="position: absolute; bottom:65%; left: 11.5%; height: 30%; min-height:100px; width: 10%;background-color: rgba(245,245,245,0.41)"
           v-bind:style="[csrch !== 'Assigned' ? {'background-color': 'rgba(245,245,245,0.41)'} : {'background-color': 'white'}]"
           v-on:click="filtered('Assigned')">
      <Wantdown :series="[parseInt(hwvsall.filter(f => f.owner._cls.indexOf('Tenant') > -1).length/hwvsall.length*100)]"></Wantdown>
      <v-text style="position: absolute; bottom: 25%; font-size: 12px">status: <br><b>Allocated</b></v-text>
    </v-btn>
    <!-- Owners Chart rescale todo-->
    <div class="rectangle" style="position: absolute; top: 35%; left: 1.5%; height: 50.5%; min-height:180px; width: 20.5%; border-radius:1%; background-color: rgba(245,245,245,0.41)">
      <LocationChart :owners="hwvsall" style="position: absolute; left:2%; height: 90%"></LocationChart>
      <v-text style="position: absolute; bottom: 8%; left: 1%; background: #f0ae2d"> &nbsp; &nbsp; &nbsp; &nbsp;&nbsp;S.R.E.&nbsp; &nbsp;&nbsp;&nbsp; &nbsp;</v-text>
      <v-text style="position: absolute; bottom: 1%; left: 1%; background: #7d67c9">&nbsp;Warriors-KD &nbsp;</v-text>
      <v-text style="position: absolute; bottom: 8%; left: 35%; background: #f1536a">&nbsp; &nbsp; &nbsp;&nbsp;Suranga&nbsp; &nbsp;&nbsp; &nbsp;</v-text>
      <v-text style="position: absolute; bottom: 1%; left: 34.58%; background: #3cc899">&nbsp; &nbsp; &nbsp;&nbsp;Sys.Eng &nbsp; &nbsp; &nbsp;&nbsp;</v-text>
      <v-text style="position: absolute; bottom: 1%; left: 68%; background: #1892ee">&nbsp; &nbsp; &nbsp;&nbsp;Net.Eng &nbsp; &nbsp; &nbsp;&nbsp;</v-text>
    </div>
    <DataTable :filteredata="filteredata" :name1="hwvsall" :search="csrch" style="position: absolute; left: 25%; right: 2%; top: 10%; bottom: 10%; overflow-y: auto">
    </DataTable>
  </div>
</template>

<script>
  import VIcon from 'vuetify/es5/components/VIcon/VIcon'
  import VBtn from 'vuetify/es5/components/VBtn/VBtn'
  import Wantdown from '../../../charts/Wantdown'
  import Quadraup from '../../../charts/quadraup'
  import Sother from '../../../charts/Sother'
  import SCompute from '../../../charts/SCompute'
  import SUnprov from '../../../charts/SUnprov'
  import Total from '../../../charts/Total'
  import DataTable from '../../UIComponents/PaperTable'
  import LocationChart from '../../../charts/LocationChart'
  export default {
    name: 'QuadraCard',
    components: {
      LocationChart,
      VBtn,
      VIcon,
      DataTable,
      Total,
      SUnprov,
      SCompute,
      Sother,
      Quadraup,
      Wantdown
    },
    props: {
      hwvsall: {
        type: Array
      }
    },
    data () {
      return {
        csrch: '',
        filteredata: this.hwvsall
      }
    },
    methods: {
      filtered (x) {
        if (this.csrch === x) this.csrch = ''
        else this.csrch = x
        this.filteredata = this.hwvsall.filter(f => f.status.comment.indexOf(x) > -1)
      }
    }
  }
</script>

<style scoped>

</style>



