<script>
  import VBtn from 'vuetify/es5/components/VBtn'
  import VIcon from 'vuetify/es5/components/VIcon'
  import VTooltip from 'vuetify/es5/components/VTooltip'
  import LocationCard from './LocationCard'
  import WorldMap from 'vue-world-map/src/App.vue'
  import VMenu from 'vuetify/es5/components/VMenu'
  import { loadProgressBar } from '../../../../node_modules/axios-progress-bar'
  import '../../../../node_modules/axios-progress-bar/dist/nprogress.css'
  import { VList, VListTile } from 'vuetify/es5/components/VList'
  export default {
    name: 'Map',
    components: {
      VMenu,
      VList,
      VListTile,
      WorldMap,
      LocationCard,
      VTooltip,
      VIcon,
      VBtn
    },
    props: {
      // show: {
      //   type: Boolean
      // },
      map_val: {
        type: Array
      },
      nnd_open: {
        type: Boolean
      }
      // vendor: {
      //   type: String
      // }
    },
    data () {
      return {
        data: [{
          US: 100,
          CA: 120,
          UK: 400
        }],
        city: 'Select DC',
        card_open: false,
        name: 'World',
        vendor: 'all',
        show: false,
        loading: false,
        reg: '',
        foundya: '',
        dcs: [
          ['Amsterdam'],
          ['Ashburn'],
          ['Berlin'],
          ['Bucharest'],
          ['Chicago'],
          ['Copenhagen'],
          ['Dallas'],
          ['Denver'],
          ['Dubai'],
          ['Dublin'],
          ['Frankfurt'],
          ['Hong Kong'],
          ['Johannesburg'],
          ['Los Angeles'],
          ['London'],
          ['Melbourne'],
          ['Miami'],
          ['Milan'],
          ['Mumbai'],
          ['New York'],
          ['Palo Alto'],
          ['Paris'],
          ['Prague'],
          ['Sao Paulo'],
          ['Seattle'],
          ['Singapore'],
          ['Sydney'],
          ['Tokyo'],
          ['Toronto'],
          ['Vancouver'],
          ['Warsaw']
        ]
      }
    },
    mounted: function () {
      this.map_val = this.$store.getters.RESERVE
      // this.loading = true
      // axios.get('http://10.211.34.12/api/v0/devices?limit=5000')
      //   .then(response => (this.map_val = JSON.stringify(response.data.payload).replace(/"serial":null/gi, '"serial": "none"').replace(/null/gi, '{ "alias": "none", "vendor": "None", "name": "None", "comment": "Free", "_cls": "none" }'))).then(x => (this.map_val = JSON.parse(this.map_val))).then(x => (this.loading = false))
      loadProgressBar()
    },
    methods: {
      entry (x) {
        this.data = this.map_val.filter(f => f.location.name.indexOf(x) > -1)
        // document.getElementById('lc').setAttribute('style', 'background: ../../assets/Mumbai.jpg')
        this.city = x
        this.card_open = true
      },
      doScroll (area, e) {
        // cross-browser wheel delta
        e = window.event || e
        var delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)))

        // Do something with `delta`
        if (delta === 1 && this.reg === '') {
          this.reg = area
          this.show = true
        }
        if (delta !== 1 && this.reg !== '') {
          this.reg = ''
          this.show = false
        }
        if (this.reg === '') this.name = 'World'
        if (this.reg === 'na') this.name = 'North America'
        if (this.reg === 'sa') this.name = 'South America'
        if (this.reg === 'as') this.name = 'Asia'
        if (this.reg === 'af') this.name = 'Africa'
        if (this.reg === 'eu') this.name = 'Europe'
        if (this.reg === 'au') this.name = 'Australia'
        e.preventDefault()
      }
    }
  }
</script>

