    /*animation-timing-function: ease-in-out;*/
    animation-iteration-count: 1;
    animation-duration: 1s;
    /*animation-direction: alternate;*/
  }
  #map img {
    animation-name: fade;
    /*animation-timing-function: ease-in-out;*/
    animation-iteration-count: 1;
    animation-duration: 1s;
    /*animation-direction: alternate;*/
  }

  #map img.bgmap {
    opacity:1 !important;
  }
  @keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  .v-menu > .v-menu__content{
    max-height: 500px;
